<template>
	<section>
		<!---->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true" :model="filters">
				<el-form-item>
					<el-input v-model="filters.name" placeholder=""></el-input>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="getListData"></el-button>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" v-on:click="funAdd"></el-button>
				</el-form-item>
			</el-form>
		</el-col>

		<!---->
		<el-table :data="listData" highlight-current-row v-loading="loading" style="width: 100%;">
			<el-table-column prop="departmentId" label="" width="100" align="center" v-if="showId">
			</el-table-column>
			<el-table-column prop="departmentName" label="" width="120"  align="center" >
			</el-table-column>
			<el-table-column prop="establishTime" label="" width="160"  align="center" :formatter="formatEstablishTime" >
			</el-table-column>
			<el-table-column prop="departmentRemark" label="" :show-overflow-tooltip="true"></el-table-column>
			<el-table-column label="" width="120" align="center">
				<template  slot-scope="scope">
					<a class="btn-operation" href="javascript:void(0)" @click="funEdit(scope.row)"></a>
					<a class="btn-operation" href="javascript:void(0)" @click="funDelete(scope.row)"></a>
				</template>
			</el-table-column>
		</el-table>
		<!---->
		<div align="center">
			<el-pagination
					@current-change="handleCurrentChange"
					:current-page="pageIndex"
					:page-size="pageSize"
					layout="total, sizes, prev, pager, next, jumper"
					:total="totalCount">
			</el-pagination>
		</div>
		<!---->
		<el-dialog :title="formTitle" :visible.sync="formVisible" :close-on-click-modal="false" @close="closeForm">
			<el-form :model="formData" label-width="80px" :rules="formRules" ref="refForm">
				<el-form-item label="" prop="departmentName">
					<el-input v-model="formData.departmentName" auto-complete="off"></el-input>
				</el-form-item>
				<el-form-item label="" prop="establishTime">
					<el-date-picker type="date" placeholder="" v-model="formData.establishTime"></el-date-picker>
				</el-form-item>
				<el-form-item label="">
					<el-input type="textarea" v-model="formData.departmentRemark"></el-input>
				</el-form-item>
			</el-form>
			<div slot="footer" class="dialog-footer">
				<el-button @click="closeForm"></el-button>
				<el-button type="primary" @click.native="fromSubmit" :loading="formLoading"></el-button>
			</div>
		</el-dialog>
	</section>
</template>
<script>
    import js from './department.js';
    export default js;
</script>

<style scoped>
	@import '../../styles/common.css';
</style>


