<template>
	<div id="app">
		<!--transitionvuejstransition-->
		<!--name="fade"fade-enter fade-enter-active,css-->
		<!--mode="out-in"-->
		<transition name="fade"
		            mode="out-in">
			<router-view></router-view>
		</transition>
	</div>
</template>

<script>
export default {
	name: 'app',
	components: {
	}
}

</script>

<style lang="scss">
body {
	/*padding444*/
	margin: 0px;
	padding: 0px;
	/*background: url(assets/bg1.jpg) center !important;*/
	/*background-size: cover;*/
	/*background: #1F2D3D;*/
	-webkit-font-smoothing: antialiased;
	background: url('assets/bg3.jpg');
	repeat: no-repeat;
	background-attachment:fixed;
	/*filter (<img>)()*/
	/* scale*/
	filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(src='assets/bg3.jpg', sizingMethod='scale');
	-ms-filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(src='assets/bg3.jpg', sizingMethod='scale');
	/*background-size px px covercontain*/
	/*no-repeatcover*/
	/*no-repeatcontain*/
	/*no-repeat*/
	background-size: cover;
	-moz-background-size: cover;
	-webkit-background-size: cover;
	font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
	font-size: 14px;
	-webkit-font-smoothing: antialiased;
}

#app {
	position: absolute;
	top: 0px;
	bottom: 0px;
	width: 100%;
}

.el-submenu [class^=fa] {
	vertical-align: baseline;
	margin-right: 10px;
}

.el-menu-item [class^=fa] {
	vertical-align: baseline;
	margin-right: 10px;
}

.toolbar {
	background: #f2f2f2;
	padding: 10px;
	//border:1px solid #dfe6ec;
	margin: 10px 0px;
	.el-form-item {
		margin-bottom: 10px;
	}
}

.fade-enter-active,
.fade-leave-active {
	/**/
	transition: all .2s ease;
	//transition: opacity .5s;
}

.fade-enter,
.fade-leave-active {
	/*opacity10*/
	opacity: 0;
}
</style>


