<template>
    <section>
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-select v-model="filters.level" placeholder="">
                        <el-option v-for="item in listLevels"
                                   :key="item.order"
                                   :label="item.name"
                                   :value="item.name">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getListData"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="funAdd"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <el-table :data="listData" highlight-current-row v-loading="loading" style="width:100%">
            <el-table-column prop="positionId" label="" width="100" align="center" v-if="showId"></el-table-column>
            <el-table-column prop="positionName" label="" width="200" align="center"></el-table-column>
            <el-table-column prop="positionLevel" label="" width="100" align="center"></el-table-column>
            <el-table-column prop="positionRemark" label="" :show-overflow-tooltip="true"></el-table-column>
            <el-table-column label="" width="120" align="center">
                <template  slot-scope="scope">
                    <a class="btn-operation" href="javascript:void(0)" @click="funEdit(scope.row)"></a>
                    <a class="btn-operation" href="javascript:void(0)" @click="funDelete(scope.row)"></a>
                </template>
            </el-table-column>
        </el-table>
        <div  align="center">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page="pageIndex"
                    :page-size="pageSize"
                    :total="totalCount"
                    layout="total, sizes, prev, pager, next, jumper">
            </el-pagination>
        </div>
        <!--@close-->
        <!-- title="",:title.title="{{formTile}}" -->
        <el-dialog :title="formTitle" :visible.sync="formVisible" @close="closeForm">
            <!--el-formv-model:modelv-modelthis.$refs.refForm.resetFields();-->
            <!--el-input:modelv-molde:modelvalidate-->
            <el-form :model="formData" :rules="formRules" label-width="80px" ref="refForm">
                <el-form-item label="" prop="positionName">
                    <el-input type="text" v-model="formData.positionName" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="positionLevel">
                    <el-select v-model="formData.positionLevel" filterable placeholder="">
                        <el-option v-for="item in addLevels"
                                   :key="item.order"
                                   :label="item.name"
                                   :value="item.name">
                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item lable="">
                    <el-input type="textarea" v-model="formData.positionRemark"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="closeForm"></el-button>
                <el-button type="primary" @click="submitForm" :loading="formLoading"></el-button>
            </div>
        </el-dialog>
    </section>
</template>
<script type="text/ecmascript-6">
    import js from './position.js';
    export default js;
</script>
<style>
    @import '../../styles/common.css';
</style>


