<template>
  <el-form :model="formData" :rules="formRules" ref="loginForm" label-position="left" label-width="0px" class="login-container">
    <h3 class="title"></h3>
    <el-form-item prop="account">
      <el-input type="text" v-model="formData.account" auto-complete="off" placeholder=""></el-input>
    </el-form-item>
    <el-form-item prop="password">
      <el-input type="password" v-model="formData.password" auto-complete="off" placeholder=""></el-input>
    </el-form-item>
    <el-row>
      <el-col :span="16">
        <el-form-item prop="code">
          <el-input type="text" v-model="formData.code" auto-complete="off" placeholder=""></el-input>
        </el-form-item>
      </el-col>
      <el-col :span="8">
        <div class="code"><img v-bind:src="imgCode" alt="" @click="changeCode()"/></div>
      </el-col>
    </el-row>

    <el-checkbox v-model="rememberFlag" class="remember"></el-checkbox>
    <el-form-item style="width:100%;">
      <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining"></el-button>
      <!--<el-button @click.native.prevent="handleReset2"></el-button>-->
    </el-form-item>
  </el-form>
</template>
<script>
    import js from './login.js';
    export default js;
</script>
<style lang="scss" scoped>
  body {
    /*padding444*/
    margin: 0px;
    padding: 0px;
    /*background: url(assets/bg1.jpg) center !important;*/
    /*background-size: cover;*/
    /*background: #1F2D3D;*/
    -webkit-font-smoothing: antialiased;
    repeat: no-repeat;
    background: url('../../assets/bg7.jpg') fixed;
    /*filter (<img>)()*/
    /* scale*/
    filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(src='../../assets/bg7.jpg', sizingMethod='scale');
    -ms-filter: progid:DXImageTransform.Microsoft.AlphaImageLoader(src='../../assets/bg7.jpg', sizingMethod='scale');
    /*background-size px px covercontain*/
    /*no-repeatcover*/
    /*no-repeatcontain*/
    /*no-repeat*/
    background-size: cover;
    -moz-background-size: cover;
    -webkit-background-size: cover;
    font-family: Helvetica Neue, Helvetica, PingFang SC, Hiragino Sans GB, Microsoft YaHei, SimSun, sans-serif;
    font-size: 14px;
    -webkit-font-smoothing: antialiased;
  }
  .login-container {
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 180px auto;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
    .code {
      margin: 0px auto 40px auto;
      text-align: center;
    }
  }
</style>


