<template>
    <section>
        <!---->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item >
                        <el-checkbox v-model="sexMan" border></el-checkbox>
                        <el-checkbox v-model="sexWoman" border></el-checkbox>
                </el-form-item>
                <el-form-item>
                    <!--filterable="true"-->
                    <el-select
                    v-model="filters.departmentId"
                    filterable
                    remote
                    clearable
                    placeholder=""
                    :remote-method="queryDepartment"
                    :loadinng="searchLoading">
                        <el-option v-for="item in dsearchOptions"
                        :key="item.id"
                        :label="item.name"
                        :value="item.id"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item>
                    <el-select
                    v-model="filters.positionId"
                    filterable
                    remote
                    clearable
                    placeholder=""
                    :remote-method="queryPosition"
                    :loading="searchLoading">
                        <el-option v-for="item in psearchOptions"
                                   :key="item.id"
                                   :label="item.name"
                                   :value="item.id">

                        </el-option>
                    </el-select>
                </el-form-item>
                <el-form-item style="width:10px;">

                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getListData"></el-button>
                    <el-button type="primary" v-on:click="funAdd"></el-button>
                </el-form-item>
            </el-form>
        </el-col>

        <!---->
        <template>
            <el-table :data="listData" highlight-current-row v-loading="loading" style="width: 100%;">
                <el-table-column prop="userId" label="" width="100" hidden="true"></el-table-column>
                <el-table-column prop="userName" label="" width="120" align="center" >
                </el-table-column>
                <el-table-column prop="userSex" label="" width="70" :formatter="formatSex"  align="center" >
                </el-table-column>
                <el-table-column prop="departmentName" label="" width="120"  align="center" >
                </el-table-column>
                <el-table-column prop="positionName" label="" width="120"  align="center" >
                </el-table-column>
                <el-table-column prop="birthdayTime" label="" :formatter="formatBirthday" width="130" align="center" >
                </el-table-column>
                <el-table-column prop="phoneNumber" label="" width="130"  align="center">
                </el-table-column>
                <el-table-column prop="houseAddress" label="" >
                </el-table-column>
                <el-table-column label="" width="120" align="center">
                    <template  slot-scope="scope">
                        <a class="btn-operation" href="javascript:void(0)" @click="funEdit(scope.row)"></a>
                        <a class="btn-operation" href="javascript:void(0)" @click="funDelete(scope.row)"></a>
                    </template>
                </el-table-column>
            </el-table>
        </template>
        <div align="center">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page="pageIndex"
                    :page-size="pageSize"
                    :total="totalCount"
                    layout="total, sizes, prev, pager, next, jumper">
            </el-pagination>
        </div>
        <el-dialog :title="formTitle" :visible.sync="formVisible" @close="closeForm" :loading="formLoading">
            <el-form :model="formData" :rules="formRules" ref="refForm" >
                <el-row>
                    <el-col :span="12">
                        <el-form-item label-width="80px" label="" prop="departmentId">
                            <el-select v-model="formData.departmentId">
                                <el-option v-for="item in departmentOptions"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="" prop="positionId">
                            <el-select v-model="formData.positionId">
                                <el-option v-for="item in positionOptions"
                                :key="item.id"
                                :label="item.name"
                                :value="item.id">
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label-width="80px" label="" prop="userName">
                            <el-input class="input-two-column" type="text" v-model="formData.userName" auto-complete="off"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label-width="80px" label="" prop="userSex">
                            <el-radio-group v-model="formData.userSex">
                                <el-radio :label="1" border></el-radio>
                                <el-radio :label="2" border></el-radio>
                            </el-radio-group>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label-width="80px" label="" prop="birthdayTime">
                            <el-date-picker v-model="formData.birthdayTime" placeholder=""></el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label-width="80px" label="" prop="phoneNumber">
                            <el-input type="text"  class="input-two-column" v-model="formData.phoneNumber"></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-form-item label-width="80px" label="" prop="idNumber">
                    <el-input type="text" v-model="formData.idNumber"></el-input>
                </el-form-item>
                <el-row>
                    <el-col :span="12">
                        <el-form-item label-width="80px" label="" prop="loginName">
                            <el-input class="input-two-column" type="text" v-model="formData.loginName"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12" v-if="addOrEdit===1">
                        <el-form-item label-width="80px" label="" prop="loginPassword">
                            <el-input class="input-two-column" type="password" v-model="formData.loginPassword"></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12" v-if="addOrEdit===2">
                        <el-form-item label-width="80px" label="">
                            <el-input class="input-two-column" type="password" v-model="formData.editLoginPassword" placeholder=""></el-input>
                        </el-form-item>
                    </el-col>
                </el-row>
                <el-form-item label-width="80px" label="" prop="houseAddress">
                    <el-input type="text" v-model="formData.houseAddress"></el-input>
                </el-form-item>
                <!--element-uiel-uoload-->
                <el-form-item label-width="80px" label="">
                    <el-row>
                        <el-col :span="24">
                            <img :src="imageUrl" class="header-image" alt />
                            <input type="file" @change="fileChange" class="input-image" ref="inputFile"/>
                        </el-col>
                    </el-row>
                </el-form-item>
                <el-form-item label-width="80px" label="">
                    <el-row>
                        <el-row :span="24">
                            <div v-if="pngMessage">PNG</div>
                            <div v-if="sizeMessage">2M</div>
                            <div v-if="pxMessage">132132</div>
                        </el-row>
                    </el-row>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="closeForm"></el-button>
                <el-button type="primary" @click="submitForm" :loading="formLoading"></el-button>
            </div>
        </el-dialog>
    </section>
</template>
<script>
    import js from './user.js';
    export default js;
</script>

<style scoped>
    .header-image {
        background: #FFFFFF;
        weidth: 132px;
        height: 132px;
        border: 1px solid #d9d9d9;

    }
    /*.header-image:hover {*/
        /*border: 1px solid #409EFF;*/
    /*}*/
    .input-image {
        position: absolute;
        top: 2px;
        left: 2px;
        width: 125px;
        height: 125px;
        opacity: 0;
    }
</style>


