<template>
    <section>
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
            <el-form :inline="true" :model="filters">
                <el-form-item>
                    <el-input v-model="filters.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-input v-model="filters.orderNumber" placeholder=""></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="getListData"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="funAdd"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="downloadModel"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary"></el-button>
                    <input type="file" @change="fileChange"  class="input-file" ref="inputFile"/>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="downloadData"></el-button>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" v-on:click="funRand"></el-button>
                </el-form-item>
            </el-form>
        </el-col>
        <el-table :data="listData" border highlight-current-row v-loading="loading" style="width:100%" :span-method="objectSpanMethod">
            <el-table-column prop="studentId" label="" width="100" align="center" v-if="showId"></el-table-column>
            <el-table-column prop="groupNo" label="" width="120" align="center"></el-table-column>
            <el-table-column prop="studentName" label="" width="120" align="center"></el-table-column>
            <el-table-column prop="parentName" label="" width="120" align="center"></el-table-column>
            <el-table-column prop="contactPhone" label="" width="230" align="center"></el-table-column>
            <el-table-column prop="orderNumber" label="" width="200" align="center"></el-table-column>
            <el-table-column fixed="right" label="" width="60" align="center">
                <template  slot-scope="scope">
                    <a class="btn-operation" href="javascript:void(0)" @click="funEdit(scope.row)"></a>
                </template>
            </el-table-column>
        </el-table>
        <div  align="center">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page="pageIndex"
                    :page-size="pageSize"
                    :total="totalCount"
                    layout="total, sizes, prev, pager, next, jumper">
            </el-pagination>
        </div>
        <el-dialog :title="formTitle" :visible.sync="formVisible" @close="closeForm">
            <el-form :model="formData" :rules="formRules" label-width="80px" ref="refForm">
                <el-form-item label="">
                    <el-input type="text" v-model="formData.groupNo" :disabled="true" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="studentName">
                    <el-input type="text" v-model="formData.studentName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="parentName">
                    <el-input type="text" v-model="formData.parentName"></el-input>
                </el-form-item>
                <el-form-item label="" prop="contactPhone">
                    <el-input type="text" v-model="formData.contactPhone"></el-input>
                </el-form-item>
                <el-form-item lable="">
                    <el-input type="text" v-model="formData.orderNumber" :disabled="true" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="closeForm"></el-button>
                <el-button type="primary" @click="submitForm" :loading="formLoading"></el-button>
            </div>
        </el-dialog>
    </section>
</template>
<script type="text/ecmascript-6">
    import js from './student.js';
    export default js;
</script>
<style>
    @import '../../styles/common.css';

    .input-file {
        position: absolute;
        top: 0px;
        left: 0px;
        width: 100px;
        height: 42px;
        opacity: 0;
    }
</style>


